<template>
  <div class="PcbSucceed">

    <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
    <div style="" class="SucceedTop">
      <img src="../../../assets/img/common/succeed.png" alt="">
      <p >提交成功</p>
    </div>
    <div class="public-btn mt60 mr30 ml30" @click="goShopping">去购物车结算</div>
    <router-link :to="{path:'/PcbHome'}"  style="" class="getBack">返回</router-link>
  </div>
</template>
<script>
  import Vue from "vue"
  import HomePageTop from "../../common/HomePageTop";

  export default {
    name: "PcbSucceed",
    components: {HomePageTop},
    data() {
      return {
        //像子组件传值
        TopTotle: { name: "添加状态",left: true,goHome:"/PcbHome",},

      }
    },
    methods: {
      goShopping(){
        this.$router.push({
          path:"ShoppingHome"
        })
      },
    },
    created() {
    }
  }

</script>

<style scoped lang="less">
  .PcbSucceed{
    .SucceedTop{
      width: 100%;height: 4.72rem;text-align: center;background: #fff;padding-top: 1.4rem;
      img{width: 1.28rem; margin: .32rem;}
      p{text-align: center;font-size:.34rem;color: #333}

    }
    .getBack{width:6.9rem;height: .96rem;background:#fff;border: 1px solid #f90;text-align: center;color: #f90;line-height: .96rem;font-size: .34rem;margin: .32rem auto;display: block}
  }
  html, both {
    background: #F4F6F9;
  }
  .HomePageTop .Head {
    background: #fff;
  }
</style>



